@import "src/global";
.contact{
   background-color: white;
   display: flex;

   @include mobile {
      flex-direction: column;
      height: 100%;
   }

   .left {
      flex: 1;
      overflow: hidden;

      img {
         height: 100%;
      }
   }

   .right {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      h2 {
         font-size: 30px;
      }

      form {
         width: 70%;
         height: 70%;
         display: flex;
         flex-direction: column;
         align-items: center;
         justify-content: space-around;

         input {
            width: 300px;
            height: 30px;
            font-size: 14px;

            @include mobile {
               width: 200px;
               height: 20px;
            }
         }

         textarea {
            width: 300px;
            height: 200px;
            font-size: 14px;

            @include mobile {
               width: 200px;
               height: 100px;
            }
         }

         button {
            width: 150px;
            height: 30px;
            background-color: crimson;
            color: white;
            border-radius: 10px;
            border: none;
            font-weight: 500;
            &:focus {
               outline: none;
            }
         }
         span {
            color: green;
         }
      }
   }
}
